:root{--white:#fff;--black:#000;--red:#ff4f5e;--light-red:#fdc6c8;--green:#b7d43f;--valid-green:#59cbb7;--near-black:#4a4a4a;--grey:#e7e5e2;--darker-grey:#e6e5e3;--light-grey:#f3f2f0;--lighter-grey:#f9f9f9;--desert-storm:#f3f2f1;--warm-grey:#959595;--blue:#629ff4;--link-color:var(--blue);--override-z-index:3;--top-z-index:2;--middle-z-index:1;--bottom-z-index:0;--base-font-family:"Open Sans",sans-serif;--strong-font-family:"Zilla Slab",Georgia,Utopia,Charter,serif;--font-size:16px;--font-size-xs:12px;--font-size-lg:24px;--font-size-xl:32px;--font-size-xxl:34px;--standard-margin:0.3rem;--wide-desktop-width:1400px;--desktop-width:76rem;--old-desktop-width:55rem;--page-margin:1rem;--input-height:2.5rem;--spinner-height:10rem;--card-shadow:rgba(0,0,0,0.09) 0px 1px 6px,rgba(0,0,0,0.09) 0px 1px 4px;--transition-duration:0.2s;--transition-duration-slow:0.5s;--easing:cubic-bezier(0.4,0,0.2,1);--header-height:4rem;--nav-shadow-overhang:0px 2px 4px rgba(0,0,0,0.03);--dataset-item-margin:0.7rem;--profile-column-margin:30px}@keyframes fade-in{0%{opacity:0;pointer-events:none}to{opacity:1;pointer-events:all}}.md-block{display:none}@media (min-width:768px){.md-block{display:block}}@media (max-width:768px){.hidden-sm-down{display:none!important}}@media (max-width:992px){.hidden-md-down{display:none!important}}@media (min-width:768px){.hidden-md-up{display:none!important}}@media (min-width:992px){.hidden-lg-up{display:none!important}}.profile-delete .top{margin-bottom:50px;display:flex;flex-direction:column}@media (min-width:768px){.profile-delete .top{flex-direction:row}}.profile-delete h2{font-family:Zilla Slab,Georgia,Utopia,Charter,serif;font-family:var(--strong-font-family);font-size:24px;font-size:var(--font-size-lg);font-weight:600}@media (min-width:768px){.profile-delete h2{max-width:60%}}.profile-delete .required{line-height:1.5;font-size:12px;font-size:var(--font-size-xs);letter-spacing:.8px;font-style:italic;color:#4a4a4a;color:var(--near-black)}@media (min-width:768px){.profile-delete .required{width:100%;text-align:center}}.profile-delete .toggle-with-info{display:flex;flex-direction:column}@media (min-width:768px){.profile-delete .toggle-with-info{flex-direction:row}}.profile-delete .toggle-with-info .toggle-container{margin-bottom:20px;flex-shrink:0;width:60%}.profile-delete .toggle-with-info .info{display:flex;flex-direction:row;font-size:12px;font-size:var(--font-size-xs);color:#4a4a4a;color:var(--near-black);letter-spacing:.5px}[dir=ltr] .profile-delete .toggle-with-info svg{margin-right:5px}[dir=rtl] .profile-delete .toggle-with-info svg{margin-left:5px}.profile-delete .toggle-with-info svg{flex-shrink:0;width:22px}.profile-delete .toggle-with-info path{fill:#959595;fill:var(--warm-grey)}.profile-delete textarea{border:1px solid #e7e5e2;border:1px solid var(--grey);border-radius:2px;padding:10px;max-width:50%;width:100%;min-height:165px;font-family:Open Sans,sans-serif;font-family:var(--base-font-family)}.profile-delete .button{border:none;margin:0 auto;height:57px;background:#629ff4;background:var(--blue)}.profile-delete .button:hover{border:1px solid #000;border:1px solid var(--black)}